import React , {useState, useEffect}from 'react';
import Avatar from "@material-ui/core/Avatar/"
import "./post.css"
import {db} from "./firebase"
import firebase from "firebase"
function Post({username,imageUrl,caption, user, postID, show}) {
	const [comment, setComment] = useState("")
	const [comments, setComments] = useState([])
	const change = ()=>{
		return show==="showing"?"notShowing":"showing"
	}
	useEffect(() => {
		let unsubscribe;
		if(postID){
			unsubscribe = db.collection("posts").doc(postID).collection("comments").orderBy('timeStamp',"asc").onSnapshot(snapshot=>{
			setComments(snapshot.docs.map(doc=>doc.data()))
			})
		}
		return ()=>{
			unsubscribe()
		};
	}, [postID])
	const postComment=(event)=>{
		event.preventDefault();
		db.collection("posts").doc(postID).collection("comments").add({
			text: comment,
			username: user.displayName,
			timeStamp: firebase.firestore.FieldValue.serverTimestamp(),
		});
		setComment("")
	}
	return (
		<div className = "post">
			<div className = "post__header">
				<Avatar className = "header__avatar" alt={username} src="/static/images/avatar/1.jpg" />
				<h4 className = "header__name">{username}</h4>
				{user?.displayName==="losangelesclpr141177"&&
				<button onClick = {()=>db.collection("posts").doc(postID).update("show", change())}>{show}</button>}
			</div>
			<img 
				className = "post__img"
				src = {imageUrl}
				alt = "pic"
			/>
			<p className = "post__caption"><strong>{username} </strong>{caption}</p>
			<hr />
			{comments.map(a=>(<p className = "comment"><strong>{a.username} </strong>{a.text}</p>))}
			{user&&
				<form className = "post__comment">
					<input className = "comment__text" value = {comment} onChange = {(event)=>setComment(event.target.value)}/>
					<button disabled = {!comment} className = "comment__button" onClick = {postComment}>Post</button>
				</form>
			}
		</div>
	)
}

export default Post