import React , {useState, useEffect}from 'react';
import './App.css';
import Post from "./Post"
import {db ,auth} from "./firebase"
import Modal from '@material-ui/core/Modal';
import { makeStyles } from '@material-ui/core/styles';
import {Button, Input} from "@material-ui/core"
import InputPost from './InputPost'
function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    width: "50vw",
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));


function App() {
  const [modalStyle] = useState(getModalStyle)
  const classes = useStyles()
  const [posts ,setposts] = useState([])
  const [signupOpen , setSignupOpen]  = useState(false)
  const [loginOpen, setLoginOpen] = useState(false)
  const [username , setUsername] = useState("")
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [user , setUser] = useState(null)
  const [show, setShow] = useState("")
  useEffect(() => {
      db.collection("posts").orderBy('timeStamp',"desc").onSnapshot(snapshot=>{
        setposts(snapshot.docs.filter(doc=>doc.data()["show"]==="showing").map(doc=>({
          id: doc.id,
          post: doc.data()
        })))
      })
    }, [])
  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged(authUser=>{
      if(authUser){
        setUser(authUser)
      }
      else{
        setUser(null)
      }
      if(authUser?.displayName==="losangelesclpr141177"){
        db.collection("posts").orderBy('timeStamp',"desc").onSnapshot(snapshot=>{
        setposts(snapshot.docs.map(doc=>({
          id: doc.id,
          post: doc.data()
        })))
      })
      }
    })
    return ()=>unsubscribe()
  }, [user ,username])

  const signup = (event)=>{
    event.preventDefault()
    auth.createUserWithEmailAndPassword(email, password)
    .then(authUser=>{
      return authUser.user.updateProfile({
        displayName: username
      })
    })
    .catch(error=>alert(error.message))
    setSignupOpen(false)
    setEmail("")
    setPassword("")
    setUsername("")
  }   
  
  const login = (event)=>{
    event.preventDefault()
    auth
    .signInWithEmailAndPassword(email, password)
    .catch(error=>alert(error.message))

    setLoginOpen(false)
    setEmail("")
    setPassword("")
  }
  return (
    <div className = "app">
      <Modal
        open={signupOpen}
        onClose={()=>setSignupOpen(false)}
      >
      <div className =  {classes.paper} style = {modalStyle}>
        <form  className = "app__signup">
          <img className = "app__signupimg" src = "https://www.instagram.com/static/images/web/mobile_nav_type_logo.png/735145cfe0a4.png"/>
            <Input 
              type = "text"
             value = {username} 
             placeholder = "username" 
             onChange = {(e)=>setUsername(e.target.value)}
             />
             <Input 
              type = "text"
             value = {email} 
             placeholder = "email" 
             onChange = {(e)=>setEmail(e.target.value)}
             />
             <Input 
              type = "password"
             value = {password} 
             placeholder = "password" 
             onChange = {(e)=>setPassword(e.target.value)}
             />
             <Button type = "submit" onClick = {signup}>SignUP</Button>
        </form>
      </div>
      </Modal>
            <Modal
        open={loginOpen}
        onClose={()=>setLoginOpen(false)}
      >
      <div className =  {classes.paper} style = {modalStyle}>
        <form  className = "app__signup">
          <img className = "app__signupimg" src = "https://www.instagram.com/static/images/web/mobile_nav_type_logo.png/735145cfe0a4.png"/>
             <Input 
              type = "text"
             value = {email} 
             placeholder = "email" 
             onChange = {(e)=>setEmail(e.target.value)}
             />
             <Input 
              type = "password"
             value = {password} 
             placeholder = "password" 
             onChange = {(e)=>setPassword(e.target.value)}
             />
             <Button type = "submit" onClick = {login}>Login</Button>
        </form>
      </div>
      </Modal>

      <div className = "header">
        <img className = "header__img" src = "https://www.instagram.com/static/images/web/mobile_nav_type_logo.png/735145cfe0a4.png" />
        {user? 
          <Button onClick = {()=>auth.signOut()}>Logout</Button>:
          <div>
            <Button onClick = {()=>setLoginOpen(true)}>Login</Button>
            <Button onClick = {()=>setSignupOpen(true)}>SignUp</Button>
          </div>
        }
      </div>
      <div className = "app__posts">
        {posts.map(({id, post})=>(<Post key = {id} postID = {id} show = {post.show} user = {user} username = {post.username} imageUrl = {post.imageUrl} caption = {post.caption}/>))}
      </div>
      
      {user?.displayName?<InputPost username = {user.displayName} />:
      <center><h3>You Can Login Or SignUp To post</h3></center>}
    </div>
  );
}

export default App;
